@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 400;
  src: local(""), url("../fonts/HelveticaNeueCyr-Roman.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Roman.woff") format("woff"); }

@font-face {
  font-family: "Helvetica";
  font-style: normal;
  font-weight: 700;
  src: local(""), url("../fonts/HelveticaNeueCyr-Bold.woff2") format("woff2"), url("../fonts/HelveticaNeueCyr-Bold.woff") format("woff"); }

html {
  box-sizing: border-box; }

*,
*::before,
*::after {
  box-sizing: inherit; }

body {
  min-width: 375px;
  margin: 0;
  font-family: "Helvetica", "Arial", sans-serif;
  font-size: 14px;
  font-weight: 400;
  background-color: #343539;
  color: #ffffff; }

.list-reset {
  padding: 0;
  margin: 0;
  list-style: none; }

.img {
  min-width: 100%;
  height: auto; }

.visually-hidden {
  position: absolute;
  width: 1px;
  height: 1px;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
  overflow: hidden; }

.button {
  background-color: #ffffff;
  color: #343539;
  text-align: center;
  border: none;
  outline: none;
  display: block; }
  .button:hover, .button:focus {
    opacity: 0.3;
    cursor: pointer; }
  .button:active {
    opacity: 0.3; }

a {
  text-decoration: none;
  color: #ffffff; }

.title-small {
  font-weight: 700;
  font-size: 24px;
  line-height: 30px;
  position: relative;
  left: 33px; }

.title-small::before {
  content: "";
  position: absolute;
  width: 24px;
  height: 24px;
  background-color: #ef483f;
  border-radius: 50%;
  color: #ffffff;
  font-size: 16px;
  line-height: 22px;
  text-align: center;
  vertical-align: middle;
  left: -32px; }

.form__route .title-small::before {
  content: "1"; }

.form__transport .title-small::before {
  content: "2"; }

.form__cargo .title-small::before {
  content: "3"; }

.form__cargo .title-small::before {
  content: "3"; }

.form__cargo .title-small::before {
  content: "3"; }

.form__time .title-small::before {
  content: "4"; }

.form__list {
  display: flex;
  width: 46%;
  flex-wrap: wrap; }

input {
  font-size: 15px;
  line-height: 18px;
  min-height: 45px; }

h2 {
  margin: 0;
  max-width: 200px; }

.form__item p {
  margin: 0;
  padding-bottom: 6px;
  font-size: 13px; }

select {
  font-size: 15px;
  line-height: 18px;
  padding: 4px 10px 0 10px;
  min-height: 46px; }

select:active {
  background-color: #ef483f;
  color: #ffffff; }

@media (max-width: 890px) {
  .title-small {
    font-size: 20px;
    line-height: 24px; }
  .form__list {
    width: 100%; }
  .form__item p {
    padding-bottom: 9px; }
  select {
    min-height: 45px; } }

.form {
  width: 100%;
  margin: 0 auto;
  padding-bottom: 129px; }

.form__container {
  margin: 0 auto;
  padding-top: 42px; }

.form__container h1 {
  font-size: 56px;
  line-height: 68px;
  font-weight: 700;
  margin: 0;
  padding-bottom: 20px;
  padding-top: 43px; }

.form__item {
  display: flex;
  flex-direction: column; }

@media (max-width: 890px) {
  .form__container h1 {
    font-size: 32px;
    line-height: 40px;
    margin: 0 auto;
    text-align: center; } }

.container {
  width: 100%;
  padding: 0 168px;
  margin: 0 auto; }
  @media (max-width: 1584px) {
    .container {
      width: 100%;
      padding: 0 45px; } }
  @media (max-width: 890px) {
    .container {
      max-width: 100%;
      padding: 0 16px; } }

.form__rout {
  width: 100%;
  margin: 0 auto; }

.form__route h2 {
  margin: 0;
  padding-bottom: 17px; }

.form__route label {
  padding-bottom: 5px; }

.form__route-item {
  width: 100%;
  padding-bottom: 16px;
  position: relative; }

.form__item-button {
  width: 16px;
  height: 2px;
  background-color: #cfcfcf;
  box-shadow: 0 6px 0 0 #cfcfcf;
  position: absolute;
  right: 0;
  top: 41px;
  margin-right: 11px;
  border: none; }

.form__transport {
  padding-top: 27px; }

.form__transport h2 {
  margin: 0;
  padding-bottom: 13px; }

/*.form__transport label{
padding-bottom: 5px;
}*/
.form__transport-list {
  width: 23%; }

.form__transport-item {
  width: 50%;
  padding-right: 8px; }

.form__transport-item:last-child {
  padding-right: 0; }

.form__transport-item option {
  font-size: 15px;
  line-height: 18px; }

@media (max-width: 890px) {
  .form__transport-list {
    width: 100%; } }

.form__cargo {
  padding-top: 42px; }

.form__cargo h2 {
  padding-bottom: 16px; }

.form__cargo-item:first-child {
  width: 100%; }

.form__cargo-list {
  width: 23%; }

.form__cargo-item {
  width: 50%;
  padding-right: 8px;
  padding-bottom: 18px; }

.form__cargo-item:first-child,
.form__cargo-item:nth-child(4),
.form__cargo-item:nth-child(5) {
  width: 100%; }

.form__cargo-item:nth-child(1),
.form__cargo-item:nth-child(3) {
  padding-right: 0; }

.form__cargo label {
  padding-bottom: 6px; }

.checkbox {
  padding-top: 4px; }

.checkbox__arrow {
  display: flex;
  padding-left: 38px;
  position: relative;
  font-size: 13px;
  line-height: 15px;
  border: none; }

.checkbox__arrow::before {
  content: "";
  width: 27px;
  height: 27px;
  border-radius: 5%;
  border: none;
  margin: 0;
  background-repeat: no-repeat;
  background-position: center;
  display: inline-block;
  vertical-align: top;
  position: absolute;
  z-index: 1;
  top: -5px;
  left: 0;
  background-color: #ef483f; }

.checkbox__arrow::after {
  content: "";
  width: 7px;
  height: 15px;
  -webkit-transform: rotate(41deg);
  transform: rotate(41deg);
  background-color: #ef483f;
  border-right: 2px solid #ffffff;
  border-bottom: 2px solid #ffffff;
  margin: 0;
  background-position: center;
  display: inline-block;
  vertical-align: top;
  z-index: 5;
  position: absolute;
  left: 9px;
  top: -3px; }

.form__temperature-list {
  padding-top: 17px;
  display: flex; }

.form__temperature-item {
  padding-right: 8px; }

.form__temperature-item:last-child {
  padding-right: 0; }

.form__temperature-item label {
  font-size: 13px;
  line-height: 15px; }

.form__temperature-list {
  display: none; }

@media (max-width: 890px) {
  .form__cargo-list {
    width: 100%; }
  .form__cargo-item:first-child,
  .form__cargo-item:nth-child(5) {
    width: 50%; }
  .form__cargo-item:nth-child(1) {
    padding-right: 8px; }
  .form__cargo-item:nth-child(2) {
    display: none; }
  .form__cargo-item:nth-child(3) {
    padding-right: 0; }
  .form__cargo-item:nth-child(4) {
    width: 100%;
    padding-right: 0; }
  .checkbox {
    padding-top: 0; }
  .checkbox__arrow::before {
    content: "";
    width: 25px;
    height: 25px; }
  .form__temperature-list {
    display: flex; }
  .form__temperature-item {
    width: 50%; }
  .form__temperature-item {
    padding-right: 8px; }
  .form__temperature-item:last-child {
    padding-right: 0; } }

.form__time {
  padding-top: 19px; }

.title-small {
  /*font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  color: $color-light;
  background-color: $color;
  border: none;*/ }

.form__time-box {
  width: 46%; }

.form__time-wrap {
  display: flex;
  justify-content: space-between;
  font-size: 13px;
  line-height: 15px; }

.form__time-wrap p {
  margin: 0;
  padding: 15px 0 8px 0; }

.form__time-list a {
  background-color: #4b4b4f;
  min-height: 44px;
  display: flex;
  justify-content: space-between;
  margin-bottom: 4px; }

.form__time-plan {
  color: #cfcfcf;
  padding-top: 17px; }

.form__time-insert {
  display: block;
  background-color: #717174;
  text-transform: uppercase;
  width: 120px;
  height: 26px;
  text-align: center;
  padding-top: 6px;
  margin-top: 9px;
  margin-right: 8px; }

.form__time-uninsert {
  position: relative;
  font-size: 15px;
  line-height: 19px;
  padding-top: 13px;
  left: 37px; }

.form__time-insert {
  font-size: 13px;
  line-height: 16px; }

.disable .form__time-insert {
  opacity: 0.3; }

.form__time-uninsert::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #343539;
  border-radius: 50%;
  border: 4px solid #ef483f;
  left: -22px; }

.disable .form__time-uninsert::before {
  content: "";
  position: absolute;
  width: 16px;
  height: 16px;
  background-color: #343539;
  border-radius: 50%;
  border: 4px solid #ffffff;
  left: -22px; }

.form__item-letter {
  padding: 12px 0 8px 0; }

.form__time-text {
  font-size: 15px;
  line-height: 18px;
  padding: 14px 0 0 14px;
  color: #cfcfcf;
  font-family: "Helvetica", "Arial", sans-serif;
  min-height: 80px; }

.form__time-list {
  width: 100%; }

.form__time-arrow {
  display: block;
  fill: #ffffff; }

@media (max-width: 890px) {
  .form__time {
    padding-top: 22px; }
  .form__time-box {
    width: 100%; }
  .form__time-text {
    padding: 14px 10px 0 14px; } }

.dropbtn {
  width: 103px;
  cursor: pointer;
  font-size: 24px;
  line-height: 30px;
  font-weight: 700;
  color: #ffffff;
  background-color: #343539;
  border: none;
  display: flex;
  justify-content: space-between;
  align-items: center; }

/* Dropdown button on hover & focus */
.dropbtn:hover, .dropbtn:focus {
  opacity: 0.3; }

/* The container <div> - needed to position the dropdown content */
.dropdown {
  position: relative;
  display: inline-block; }

/* Dropdown Content (Hidden by Default) */
.dropdown-content {
  display: none;
  position: absolute;
  z-index: 1;
  overflow: auto; }

/* Links inside the dropdown */
/* Change color of dropdown links on hover */
/* Show the dropdown menu (use JS to add this class to the .dropdown-content container when the user clicks on the dropdown button) */
.show {
  display: block; }

@media (max-width: 890px) {
  .dropbtn {
    font-size: 20px;
    line-height: 24px; } }
